import React from "react";
import RubberItem from "./RubberItem";

interface RubberText {
  children: string;
  className?: string;
}

export default function RubberText({ children, className }: RubberText) {
  return (
    <div className="flex flex-row">
      {children.split("").map((c, index) => (
        <RubberItem key={index} className={className}>
          {c}
        </RubberItem>
      ))}
    </div>
  );
}
