import React from "react";
import "./Loading.css";

interface LoadingProps {
  className?: string;
}

export default function Loading({ className }: LoadingProps) {
  return (
    <div
      className={"fixed h-screen w-screen flex justify-center bg-white z-[1000] ".concat(
        className ?? ""
      )}
    >
      <img className="my-auto " src="./loading.gif"></img>
    </div>
  );
}
