import React, { Children } from "react";

interface ButtonProps {
  className?: string;
  children?: React.ReactNode;
  onClick?: () => any;
}

function PrimaryButton(props: ButtonProps) {
  return (
    <button
      onClick={props.onClick}
      className="text-gray-900 bg-white hover:bg-gray-100 border border-secondery focus:ring-4 focus:outline-none focus:ring-gray-100 font-medium rounded-lg text-sm px-10 py-2.5 text-center inline-flex items-center dark:focus:ring-primary dark:bg-gray-800 dark:border-secondery dark:text-white dark:hover:text-secondery"
    >
      {props.children}
    </button>
  );
}

export default PrimaryButton;
